* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

#root{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App {
  width: 800px;
}

.post{
  display: flex;
  padding: 15px;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  border: 2px solid teal;
  margin: 15px;
}


.post-enter {
  transform: translateX(-350px);
}

.post-enter-active {
  transform: translateX(0px);
  transition: all 500ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  transform: translateX(-150px);
  transition: all 500ms ease-in;
}
.post__btns {
  display: flex;
}

.page__wrapper{
  margin-top: 30px;
  display: flex;
}

.page {
  padding: 10px;
  margin-right: 1px;
  border: 1px solid teal;
  cursor: pointer;
}

.page__current{
  border: 2px solid orange;
  font-weight: bold;
}

.navbar{
  height: 50px;
  width: 100vw;
  display: flex;
  align-items: center;
  padding: 0 15px;
  background-color: lightgrey;

}

.navbar__link{
  margin-left: 5px
}
